.first-screen {
	position: relative;
	margin-bottom: 96px;

	.first-screen-nav {
		display: flex;
		max-width: 1568px;
		width: 100%;
		justify-content: space-between;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
}

.first-screen-slider {
	.slick-slide {
		background-size: cover;
		background-repeat: no-repeat;
	}

	.slick-dots {
		position: absolute;
		left: 50%;
		transform: translate(-50%, 0);
		bottom: 42px;
	}
}
.first-screen-title {
	font-size: 56px;
	font-weight: 700;
	line-height: 126%;
	text-transform: uppercase;
	max-width: 463px;
	margin-bottom: 8px;
}

.first-screen-subtitle {
	height: 54px;
	font-size: 20px;
	font-weight: 300;
	line-height: 135.5%;
	max-width: 345px;
	margin-bottom: 37px;
}
