header {
	.navbar {
		padding-top: 25px;
		padding-bottom: 39px;
	}
	.navbar-brand {
		padding: 0;
		margin: 0;
	}
	.navbar-light {
		.navbar-nav {
			margin: 5px auto;
			max-width: 619px;
			width: 100%;
			justify-content: space-between;
			.nav-link {
				color: #ffffff;
				&:hover {
					color: $color-secondary;
				}
			}
		}
	}
	.navbar-light .navbar-toggler-icon {
		background-image: none;
	}
	.navbar-toggler {
		&:focus {
			box-shadow: none;
		}
	}
	.navbar-collapse {
		text-align: center;
	}
}
